    sans-serif;
margin: 0;
padding: 0;
-webkit-text-decoration-skip-ink: "auto";
        text-decoration-skip-ink: "auto";
} 

:root {
--gray-900: #1a202c;
--gray-800: #2d3748;
--gray-700: #4a5568;
