--red-200: #fed7d7;
--red-300: #feb2b2;
--red-400: #fc8181;
--red-500: #f56565;
--red-600: #e53e3e;
--red-700: #c53030;
--red-800: #9b2c2c;
--red-900: #742a2a;
--blue-100: #ebf8ff;
--blue-200: #bee3f8;
--blue-300: #90cdf4;
--blue-400: #63b3ed;
--blue-500: #4299e1;
--blue-600: #3182ce;
--blue-700: #2b6cb0;
--blue-800: #2c5282;
--blue-900: #2a4365;
--white: white;
--red: #f56565;
--text-decoration-color: var(--gray-400);
--text-color: var(--gray-800);
--focus-ring-color: var(--blue-500);
}

body {
padding: 0;
margin: calc((100vh / 25) * 1.563) calc((100vw / 25) * 1.563);
background-color: white;
font-weight: 400;
line-height: 1.563;
color: #2d3748;
color: var(--text-color);
caret-color: #2d3748;
caret-color: var(--text-color);
}

