/* Typography
––––––––––––––––––––––––––––––––– */

h1,
h2,
h3,
h4,
h5,
h6 {
margin-bottom: 1rem;
margin-top: 1em;
font-weight: bold;
}

h1 {
font-size: 3.052rem;
letter-spacing: -0.15rem;
line-height: 1;
}

h2 {
font-size: 2.441rem;
letter-spacing: -0.12rem;
line-height: 1.2;
}

h3 {
font-size: 1.953rem;
letter-spacing: -0.09rem;
line-height: 1.2;
}

h4 {
font-size: 1.563rem;
letter-spacing: -0.06rem;
line-height: 1.3;
}

h5 {
font-size: 1.25rem;
letter-spacing: -0.03rem;
line-height: 1.4;
}

h6 {
font-size: 1rem;
letter-spacing: 0;
line-height: 1.5;
}

p {
margin-bottom: 1.563rem;
}

p > *:last-child {
margin-bottom: 0;
}

a {
color: inherit;
text-decoration: underline;
text-decoration-color: #cbd5e0;
text-decoration-color: var(--text-decoration-color);
-webkit-text-decoration-color: #cbd5e0;
-webkit-text-decoration-color: var(--text-decoration-color);
border-radius: 5px;
text-underline-offset: 2px;
text-decoration-thickness: 2px;
}

@media (hover: hover) {
a:hover {
    -webkit-text-decoration-color: #2d3748;
            text-decoration-color: #2d3748;
    -webkit-text-decoration-color: var(--gray-800);
            text-decoration-color: var(--gray-800);
}
}

@media (hover: hover) and (prefers-color-scheme: dark) {
a:hover {
    -webkit-text-decoration-color: white;
            text-decoration-color: white;
}
}

@media (prefers-color-scheme: dark) {
a {
    text-decoration-color: #4a5568;
    text-decoration-color: var(--gray-700);
    -webkit-text-decoration-color: #4a5568;
    -webkit-text-decoration-color: var(--gray-700);
}
}

a:focus-visible {
box-shadow: 0 0 0 2px #4299e1;
box-shadow: 0 0 0 2px var(--focus-ring-color);
outline: none;
}

small {
font-size: 0.888rem;
}

hr {
border: 1px solid #4a5568;
border: 1px solid var(--gray-700);
margin: 3.052rem 0;
}

