    font-family: "SFMono-Regular", Consolas, "Liberation Mono", Menlo, Courier,
        monospace;
    font-size: 0.8rem;
    white-space: nowrap;
    background: #f7fafc;
    background: var(--gray-100);
    padding: 0 0.328rem;
    display: inline-block;
    vertical-align: middle;
    border-radius: 10px;
    }
    
    @media (prefers-color-scheme: dark) {
    code {
        background-color: #4a5568;
        background-color: var(--gray-700);
    }
    }
    
    p > code {
    white-space: normal;
    }
    
    pre > code {
    line-height: 1.563em;
    display: block;
    padding: 1rem;
    white-space: pre;
    margin-bottom: 1.563rem;
    overflow: scroll;
    }
    
    /* Forces a new-line at the end of a code block for layout purposes. */
    pre > code::after {
    content: " ";
    }
    
    /* Blockquote
    ––––––––––––––––––––––––––––––––– */
    blockquote {
    border-left: 0.25rem solid #f7fafc;
    border-left: 0.25rem solid var(--gray-100);
    padding: 0 1rem;
    margin-bottom: 1.563rem;
    }
    
    @media (prefers-color-scheme: dark) {
    blockquote {
        border-left: 0.25rem solid #4a5568;
        border-left: 0.25rem solid var(--gray-700);
    }
    }
    
    /* List
    ––––––––––––––––––––––––––––––––– */
    ul {
    margin: 0;
    padding: 0 1px;
    list-style: disc outside;
    -webkit-font-feature-settings: "tnum";
            font-feature-settings: "tnum";
    font-variant-numeric: tabular-nums;
    }
    
    ol {
    list-style: decimal outside;
    }
    
    ol,
    ul {
    padding-left: 0;
    margin-top: 0;
    margin-bottom: 1.563rem;
    }
    
    li {
    list-style-position: inside;
    }
    
    /* Keyboard
    ––––––––––––––––––––––––––––––––– */
    kbd {
    display: inline-block;
    padding: 0 0.328rem;
    font-family: "SFMono-Regular", Consolas, "Liberation Mono", Menlo, Courier,
        monospace;
    font-size: 0.64rem;
    color: #4a5568;
    color: var(--gray-700);
    vertical-align: middle;
    background-color: #f7fafc;
    background-color: var(--gray-100);
    border: solid 1px #e2e8f0;
    border: solid 1px var(--gray-300);
    border-bottom: solid 2px #a0aec0;
    border-bottom: solid 2px var(--gray-500);
    border-radius: 5px;
    }
    
    /* Abbreviation
    ––––––––––––––––––––––––––––––––– */
    abbr {
    text-decoration: none;
    border-bottom: 2px dashed #718096;
    border-bottom: 2px dashed var(--gray-600);
    }
    
    @media (hover: hover) {
    abbr:hover {
        cursor: help;
    }
    }
    
    .error {
    font-size: 12px;
    color: #e53e3e;
    color: var(--red-600);
    margin-top: 0.25rem;
    }
    
    @media (prefers-color-scheme: dark) {
    .error {
        color: #fc8181;
        color: var(--red-400);
    }
    }
    
    button:disabled {
    opacity: 0.5;
    cursor: not-allowed !important;
    }
    
    button + button {
    margin-left: 0.5rem;
    }
    
    button.secondary,
    button[type="reset"] {
    background-color: #e2e8f0;
    background-color: var(--gray-300);
    border: 2px solid #e2e8f0;
    border: 2px solid var(--gray-300);
    color: #1a202c;
    color: var(--gray-900);
    }
    
    button.secondary:hover,
    button[type="reset"]:hover {
    background-color: #cbd5e0;
    background-color: var(--gray-400);
    }
    .move {
        padding-right: 0px;
        justify-content: center;
    }
    
    
