.motherland__navbar{
    display:flex;
    justify-content: space-between;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.834);
}

.motherland__navbar-links{
    flex: 1 1;
    display:flex;
    justify-content: flex-end;
    align-items: center;
    list-style: none;
}

.motherland__navbar-links_logo {
    justify-content: flex-start;
    margin-right: 2rem;
}

.motherland__navbar-links_logo img {
    width: 100%;
    height: 50px;
}

.motherland__navbar-links_container {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
}

.motherland__navbar-links_container p,
.motherland__navbar-menu_container p {
   color: #fff;
   font-family: var(--font-family);
   font-weight: 500;
   font-size: 18px;
   line-height: 25px;
   text-transform: capitalize;
   margin: 0 1rem;
   cursor: pointer;
   text-align: right;
}
.motherland__navbar-menu_container button {
    padding: 0.5rem 1rem;
    color: #fff;
    background: #FF4820;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    border: none;
    outline: none;
    cursor: pointer;
    border-radius: 5px;
}

.motherland__navbar-menu {
    margin-left: 1rem;

    display: none;
    position: relative;
}

.motherland__navbar-menu svg {
    cursor: pointer;
}

.motherland__navbar-menu_container {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;
    
    text-align: end;
    background: var(--color-footer);
    padding: 2rem;
    position: absolute;
    right: 0;
    top: 40px;
    margin-top: 1rem;
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0px 0px 5px rgba(0,0, 0,0.2);
}

.motherland__navbar-menu_container p {
    margin: 1rem 0;
} 
@media screen and (max-width: 800px) {
    .motherland__navbar-links_container {
        display: none;
    }

    .motherland__navbar-menu {
        display: flex;
    }
}

@media screen and (max-width: 700px) {
    .motherland__navbar {
        padding: 2rem 4rem;
    }
}

@media screen and (max-width: 550px) {
    .motherland__navbar {
        padding: 2rem;
    }

    .motherland__navbar-sign {
        display: none;
    }

    .motherland__navbar-menu_container {
        top: 20px;
    }
}


